import React,{Component} from "react"
import Profile from "./Pages/Profile"
import Contact from "./Pages/Contact"
import Skills from "./Pages/Skills"
import Career from "./Pages/Career"
import Works from "./Pages/Works"


export default function Page({item}){
    if(item==="profile"){
    return <div className="page"> <Profile/> </div>;
    }else if(item==="skills"){
        return <div className="page"> <Skills/> </div>;
    }else if(item==="works"){
        return <div className="page"><Works/></div>
    }else if(item==="career"){
        return <div className="page"> <Career/></div>
    }else if(item==="contact"){
        return <div className="page"><Contact/></div>
    }else{
        return <div className="page"> <h1>404{item}</h1> </div>
    }
}