import React, { Component } from 'react';
import LeftDrawer from "./components/LeftDrawer.js"
import './css/App.css';
import Page from "./components/Page.js"
import Container from "@material-ui/core/Container"
class App extends Component {
  constructor(){
    super()
    this.bindRouter = this.router.bind(this);
    this.state={
      left:false,
      selected:"profile"
    }
  }
  router(target){
    this.setState({selected: target})
  }

  render() {
    return (
      <div className="App">
        <LeftDrawer router={this.bindRouter}/>
          <Page item={this.state.selected} />

        <footer className="App-footer">
          <div className="copyRight">
            <p>2019 Copyright Yoichito Ito All Right Reserved</p>
          </div>
        </footer>
      </div>
    );
  }
}

export default App;