import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MailIcon from '@material-ui/icons/Mail';
import {Link,Face,Build,Notes,Gesture} from "@material-ui/icons"
import MenuIcon from "@material-ui/icons/Menu"
import {Toolbar,IconButton} from "@material-ui/core"
import Page from "./Page.js"
const useStyles = makeStyles({
  list: {
    width: 300,
  },
  fullList: {
    width: 'auto',
  },
});

export default function LeftDrawer({router}) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
   });
  


  const toggleDrawer = (open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, left: open });
  };
  const showSelectedPage = (target) => event =>{
      router(target)
      } 

  const sideList = () => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
          <ListItem button key="profile" onClick={showSelectedPage("profile")}>
              <ListItemIcon><Face/></ListItemIcon>
              <ListItemText>Profile</ListItemText>
          </ListItem>
          <ListItem button key="skills" onClick={showSelectedPage("skills")}>
              <ListItemIcon><Build/></ListItemIcon>
              <ListItemText>Skills</ListItemText>
          </ListItem>
          <ListItem button key="works" onClick={showSelectedPage("works")}>
              <ListItemIcon><Gesture/></ListItemIcon>
              <ListItemText>Works</ListItemText>
          </ListItem>
          <ListItem button key="career" onClick={showSelectedPage("career")}>
              <ListItemIcon><Notes/></ListItemIcon>
              <ListItemText>Career</ListItemText>
          </ListItem>
      </List>
      <Divider />
      <List>
          <ListItem button key="contact" onClick={showSelectedPage("contact")}>
              <ListItemIcon><MailIcon/></ListItemIcon>
              <ListItemText primary="Contact"></ListItemText>
          </ListItem>
      </List>
    </div>
  );

  
  return (
    <div className="header">
    
    <Toolbar className="Navbar with-Burger">
      <IconButton edge="end" onClick={toggleDrawer(true)}><MenuIcon/></IconButton>
    </Toolbar>
    <Drawer open={state.left} onClose={toggleDrawer(false)}>
        {sideList()}
    </Drawer>
    </div>
  );
}